<template>
  <ul class="todo-list">
    <!-- completed: 完成的类名 -->
    <li :class="{ completed: item.isDone }" v-for="item in arr" :key="item.id">
      <div class="view">
        <input class="toggle" type="checkbox" v-model="item.isDone" @change="(val) => changeLine(val,item.id)"/>
        <label>{{ item.name }}</label>
        <button class="destroy" @click="deleteList(item.id)"></button>
      </div>
    </li>
  </ul>

</template>

<script>
export default {
  props: ['arr'],
  methods: {
    changeLine (id, e) {
      this.$emit('isDoneList', { id: id.target.checked, value: e })
      console.log(e)
    },
    deleteList (e) {
      this.$emit('isDelate', e)
    }
  }

}

</script>
