<template>
  <footer class="footer">
    <span class="todo-count">剩余<strong>数量值{{ id }}</strong></span>
    <ul class="filters">
      <li>
        <a :class="{ selected: isLight === 'All'}" href="javascript:;" @click="all">全部</a>
      </li>
      <li>
        <a href="javascript:;" :class="{ selected: isLight === 'no'}" @click="no">未完成</a>
      </li>
      <li>
        <a href="javascript:;"  :class="{ selected: isLight === 'ok'}" @click="ok">已完成</a>
      </li>
    </ul>
    <button class="clear-completed" @click="delCompleted">清除已完成</button>
  </footer>
</template>

<script>
export default {
  props: ['farr', 'id'],
  data () {
    return {
      isLight: 'All'
    }
  },
  methods: {
    ok () {
      this.isLight = 'ok'
      const okList = this.farr.filter(item => item.isDone)
      this.$emit('tranOkList', okList)
    },
    no () {
      this.isLight = 'no'
      const noList = this.farr.filter(item => !item.isDone)
      this.$emit('tranNoList', noList)
    },
    all () {
      this.isLight = 'All'
      const allList = this.farr

      this.$emit('tranAllList', allList)
    },
    delCompleted () {
      const ComList = this.farr.filter(item => !item.isDone)
      this.$emit('tranComList', ComList)
    }
  }
}
</script>
