<template>
  <header class="header">
    <h1>todos</h1>

    <input id="toggle-all" class="toggle-all" type="checkbox" >
    <label for="toggle-all"></label>
    <input
      class="new-todo"
      placeholder="输入任务名称-回车确认"
      autofocus
      @keyup.enter="getValue"
      v-model="value"
    />
  </header>
</template>

<script>
export default {
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      value: ''
    }
  },
  methods: {
    getValue () {
      this.$emit('inputValue', this.value)
      this.value = ''
    }

  }
}
</script>
