<template>
  <div class="todoapp">
    <TodoHeader @inputValue="getinputValue"
    :id="list.id"></TodoHeader>
  <TodoMain
:arr="newArr"

@isDoneList="getChange"
@isDelate="getisDelete"

  ></TodoMain>

  <TodoFooter
  :farr="list"
  :id="newArr.length"
  @tranOkList="getOkList"
  @tranNoList="getNoList"
  @tranAllList="getAllList"
  @tranComList="getComList"
  ></TodoFooter>
  </div>

</template>

<script>
import './style/base.css'
import './style/index.css'
import TodoMain from '@/components/TodoMain'
import TodoHeader from '@/components/TodoHeader'
import TodoFooter from '@/components/TodoFooter'
export default {
  name: 'App',
  components: {
    TodoMain, TodoHeader, TodoFooter
  },
  data () {
    return {
      list: [
        { id: 100, name: '吃饭', isDone: true },
        { id: 201, name: '睡觉', isDone: false },
        { id: 103, name: '打豆豆', isDone: true }
      ],
      newArr: []
    }
  },
  methods: {
    getChange (e) {
      this.list.some((item) => {
        if (e.value === item.id) {
          item.isDone = e.id
        }
        return true
      })
    },
    getinputValue (e) {
      if (e) {
        const newArr = { id: this.list[0].id++, name: e, isDone: false }
        this.list.push(newArr)
      }
    },
    getisDelete (e) {
      this.newArr = this.newArr.filter(item => item.id !== e)
      this.list = this.newArr
    },
    getOkList (e) {
      this.newArr = e
    },
    getNoList (e) {
      this.newArr = e
    },
    getAllList (e) {
      this.newArr = e
    },
    getComList (e) {
      this.list = e
      this.newArr = this.list
    }
  },
  created () {
    this.newArr = this.list
  }
}
</script>

